<template>
    <div class="login container">
        <h1>Login</h1>

        <b-alert
            v-if="message"
            class="my-2 text-left"
            :show="dismissCountDown"
            dismissible
            variant="danger"
            @dismissed="dismissCountDown=0"
            @dismiss-count-down="countDownChanged"
            >
            <strong>{{ message }}</strong>
        </b-alert>

        <form @submit.prevent="login">
            <input class="form-control my-2" type="email" name="email" placeholder="Email" autofocus v-model="user.email">
            <input class="form-control my-2" type="text" name="password" placeholder="Password" v-model="user.password">
            <b-button variant="primary" class="btn-block" type="submit">Login</b-button>
        </form>

    </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
    name: 'Login',
    data() {
        return {
            user: { email: 'john@doe.com', password: '123123' },
            message: '',
            dismissSecs: 5,
            dismissCountDown: 0
        }
    },
    methods: {
        ...mapActions(['saveUser']),
        login() {
            this.axios.post('/login', this.user)
                .then(response => {
                    const token = response.data.token 
                    this.saveUser(token)
                })
                .catch(errors => {
                    this.message = errors.response
                    this.showAlert()
                })
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs
        }    
    }
}
</script>

